.app {
  margin: 7px auto;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 14px);
}

.main-content-wrapper {
  display: flex;
  width: 100%;
  justify-content: center;
  height: 100%;
}
.main-content {
  width: 60%;
  border-radius: 25px;
}

.render-area {
  width: 100%;
  height: 0;
  overflow: scroll;
  flex-shrink: 0;
}
.render-area-height-transition {
  height: calc(100% - 51px);
  transition: height 2s;
  margin-top: 0;
}
.signature {
  text-align: center;
  font-size: 3em;
  font-weight: bold;
  white-space: nowrap;
  height: calc(100% - 51px);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.signature span {
  padding-bottom: 51px;
}
.message-wrapper {
  color: black;
  display: none;
  padding: 50px 30px;
  font-size: 1.3em;
  flex-shrink: 0;
}
.img-avatar {
  width: 121px;
  border-radius: 100%;
  border: 1px solid black;
}
.message-info-text {
  width: 100%;
  padding: 10px;
}
.message-top-line-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  color: #06c;
  padding-bottom: 25px;
}

.input-field-wrapper {
  background-color: white;
  display: flex;
  border-radius: 25px;
  border-top: 1px solid black;
  height: 50px;
}
.input-field-icons-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5%;

  margin-left: 25px;
}
.input-field-icon {
  height: 40px;
}
.input-field-text {
  width: 75%;
  margin-left: 5%;
  border: none;
  background-color: white;
  font-size: 1em;
}
input:focus {
  outline: none;
  padding: 0;
}
.buttons-wrapper {
  display: none;
  align-items: center;
  justify-content: center;
  width: calc(90% - 50px);
}
.button {
  margin-right: 5px;
  font-size: 1em;
  font-weight: bold;
  padding: 10px;
  background-color: gray;
  border: 1px solid #0a0022;
  border-radius: 7px;
  white-space: nowrap;
}
.button:hover {
  color: black;
}
